.page3 {
    background: $blue;
    @include AlignChild(column, center, center);
}

.page3-container{
    background:$blue;
    padding: 100px 0;
    width: 100%;
    max-width: 1600px;
    @include AlignChild(column, center, center);
    h2{
        text-align: center;
        font-family: $fontTR;
        color: $white;
        letter-spacing: 5px;
        font-size: 2rem;
        margin-bottom: 100px;
        padding: 0 25px;
    }
    .activities-container{
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        margin-bottom: 100px;       
        position: relative;
        width: 100%;
        z-index: 1;
        @media screen and (max-width:900px) {
                grid-template-columns: 1fr;
                margin-bottom:0px;
        }
        &::after{
            content: "";
            position: absolute;
            width: 500px;
            height: 500px;
            border-radius: 50%;
            background: $blue;
            filter: brightness(0.85);
            top: -25px;
            right: 50px;
            z-index: -1;
            animation: circleGrow 30s ease-in infinite;
            @media screen and (min-width:1400px) {
                    width: 800px;
                    height: 800px;
                }
            @media screen  and (max-width:700px){
                width: 400px;
                height: 400px;
            }
            @media screen  and (max-width:500px){
                width: 320px;
                height: 320px;
            }
        }
        &::before{
            content: "";
            position: absolute;
            width: 350px;
            height: 350px;
            border-radius: 50%;
            background: $blue;
            filter: brightness(0.9);
            bottom: 25px;
            left: 50px;
            z-index: -1;
            animation: circleGrow 20s ease-in infinite;
            @media screen and (min-width:1400px) {
                    width: 650px;
                    height: 650px;
                }
            @media screen  and (max-width:500px){
                width: 250px;
                height: 250px;
            }
        }
        @keyframes circleGrow {
            0%{
                transform: scale(0.7)
            }
            50%{
                transform: scale(1.2);
            }
            100%{
                transform: scale(0.7)
            }
        }

        .activity{
            @include AlignChild(column,center,center);
            position: relative;
            z-index: 1;

            .image-activity{
                width: 270px;
                height: 320px;
                @media  screen and (min-width:1400px) {
                    width: 320px;
                    height: 400px;
                }
                @media screen and (max-width:1050px) {
                    width: 220px;
                    height: 270px;
                }
                @media screen and (max-width:500px) {
                    width: 180px;
                    height: 230px;
                }

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border-radius: 45px;
                }
            }
            .link{
                position: relative;
                background: rgba(233, 229, 229, 0.8);
                width: 200px;
                height: 200px;
                border-radius: 50%;
                padding: 20px;
                @include AlignChild(column, center, center);
                text-align: center;
                font-size: 1.5rem;
                font-family: $fontR;
                backdrop-filter: blur(5px);
                @media screen and (min-width:1400px) {
                        width: 250px;
                        height: 250px;
                    }
                @media screen and (max-width:1050px) {
                    width:150px;
                    height: 150px;
                    font-size: 1.1em;
                }
                @media screen and (max-width:500px) {
                    width: 120px;
                    height: 120px;
                }
                h4{
                    color: black;
                }
            }

            &:nth-child(1){
                top: 50px;
                @media screen and (max-width:900px) {
                   top: 0;
                   right: 180px;
                }
                 @media screen and (max-width:650px) {
                   right: 100px;
                }
                @media screen and (max-width:500px) {
                   right: 80px;
                }

                .image-activity{
                    transform: rotate(-10deg);
                }
                .link{
                    bottom: 35px;
                    right: 35px;
                }
            }
            &:nth-child(2){
                top: 140px;
                @media screen and (max-width:900px) {
                   top: -50px;
                   left: 150px;
                }
                @media screen and (max-width:650px) {
                   left: 100px;
                   top: -60px;
                }
                 @media screen and (max-width:500px) {
                   left: 80px;
                   top: -80px;
                }

                .image-activity{
                    transform: rotate(-1deg);
                    @media screen and (max-width:900px) {
                        transform: rotate(5deg);
                }
                }
                .link{
                    bottom: 25px;
                    right: 15px;
                }
            }
            &:nth-child(3){
                top: 90px;
                @media screen and (max-width:900px) {
                   top:-100px;
                   right: 130px;
                }
                @media screen and (max-width:650px) {
                   right: 120px;
                }
                 @media screen and (max-width:500px) {
                   right:80px;
                }

                .image-activity{
                    transform: rotate(7deg);
                    @media screen and (max-width:900px) {
                        transform: rotate(-3deg);
                }
                }
                .link{
                    bottom: 30px;
                    left: 25px;
                }
            }
        }
    }
   .btn-activity{
    z-index: 1;
    button {
            font-size: 1.3rem;
        }
   }
}